.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

.html {
  background-color: #1e1e1e;
}

::-webkit-scrollbar-thumb {
  background-color: #343434;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #434242;
}