.payment-content {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  height: 500px;
  max-width: 1262px;

  display: flex;
  flex-direction: row;
  gap: 20px;
}

.payment-main-paper {
  padding: 24px;
  flex: 1;
  min-height: 215px;

  /*gap: 24px;*/

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  border-radius: 8px !important;
  box-shadow: none !important;
}

.payment-main-windows {
  display: flex;
  flex-direction: row;
  flex: 1;
  gap: 24px;
}

.payment-copy-line {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center
}

.payment-main-window {
  display: flex;
  flex-direction: column;
  gap: 45px;

  padding: 32px 24px;

  flex: 2;

  border-radius: 16px !important;
  box-shadow: none !important;
}

.secondary-windows {
  display: flex;
  flex-direction: column;
  gap: 24px;
  flex: 1;
}

.secondary-window {
  padding: 24px;
  border-radius: 16px !important;
  box-shadow: none !important;
  flex: 1;
}

.payment-footer {
  z-index: 1000;
  margin: auto;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 15px 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-between
}

.payment-container {
  margin: 10px;
  position: fixed;
  z-index: 900;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
}

.payment-buttons {
  display: flex;
  flex-direction: row;
  gap: 24px;
}



@media (max-width: 1116px) {
  .payment-container {
    margin: 80px 10px 10px 10px;
    position: relative;

    display: flex;
    flex-direction: column;
    flex: 1 1;
  }

  .payment-main-window {
    gap: 20px;
    padding: 32px 17px;
  }

  .payment-content {
    margin: auto;
    position: unset;

    height: unset;
    flex-direction: column;
    max-width: 400px;
    box-sizing: content-box;
  }

  .payment-main-windows {
    flex-direction: column;
  }

  .payment-buttons {
    flex-direction: column;
  }

  .payment-footer {
    position: relative;
    width: 100%;
  }
}
