.container {
    margin: 10px;
    position: fixed;
    z-index: 900;
    right: 0;
    bottom: 0;
    top: 0;
    left: 0;
}

.input-screen {
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    max-width: 500px;
    max-height: fit-content;
    height: fit-content;
    box-sizing:content-box;

    border-radius: 20px !important;
    box-shadow: none !important;

    display: flex;
    flex-direction: column;
    padding: 50px 7%;
    gap: 40px;
}

.title {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px
}

.body {
    display: flex;
    flex-direction: column;
    width: 100%;  /* это надо */
    gap: 20px;

    padding-top: 6px;
    max-height: 500px;
    overflow-y: auto;
}

@media(max-width: 500px) {
    .body {
        max-height: 300px;
    }
}

.button {
    width: 100%;
}

.buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    gap: 20px;
}

.text-fields {
    display: flex;
    flex-direction: row;
    gap: 20px
}

@media(max-width: 590px) {
    .text-fields {
        flex-direction: column;
    }
}
