.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    height: 70px;
    box-shadow: none !important;
}

.menu-button {
    width: 40px;
    height: 40px;
}

.left-side {
    display: flex;
    flex-direction: row;
    gap: 30px;
    align-items: center;
}

.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000
}

.logo {
    height: 40px;
}