.panel {
  display: flex;
  flex-direction: column;
  flex: 1;
  gap: 40px;
  margin: 100px 30px 0 30px;
}

.side-bar {
  position: fixed;
  left: 0;
  top: 70px;
  bottom: 0;

  display: flex;
  width: 200px;
  margin: 30px;
  flex-direction: column;
  justify-content: space-between;

  gap: 25px;

  box-shadow: none !important;
}

.page-selector {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.bottom-settings {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.page {
  display: flex;
  flex-direction: column;
  gap: 32px;
  flex: 1;
  margin-bottom: 30px;
}

.modal-container {
  margin: 10px;
}

.action-tab {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 16px;
  gap: 5px;

  border-radius: 16px !important;
  box-shadow: none !important;
  justify-content: space-between;
}

.action-tab-left-section {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.action-tab-right-section {
  display: flex;
  flex-direction: row;
  gap: 20px;
  flex: 1;
  justify-content: end
}

.search {
  width: 100%;
  max-width: 250px;
}

.filters {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}

.custom-select {
  display: flex;
  flex-direction: row;
  gap: 20px
}

.table-cell {
  overflow-x: hidden;
  /*white-space: normal;*/
  /*word-wrap: break-word;*/
  text-overflow: ellipsis;
}

.drawer-content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 250px;
  padding: 15px;
  margin-top: 15px;
}

.loading-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  height: 100%;
  flex: 1;
}

.bank-account-stats {
  display: flex;
  flex-direction: row;
  gap: 40px;
  width: 100%;
  justify-content: center
}

.bank-account-stats-item {
  display: flex;
  flex-direction: row;
  gap: 10px
}